/*
App main file
*/
// import styles
import '../css/style.less';
import 'bootstrap';
// import custom scripts
import './scripts/reactSPA';
import './scripts/footer-functions';
import './scripts/skillSet';
import './scripts/toTop';
import {mobileNav} from './scripts/mobileNav';
// for small screens
$('i#mobileNav').on('click', function() {
  mobileNav();
});
// import backbone/react
import Router from './router/main';
import {HomeController, CvController} from './controller/index.js';
import MainView from './view';


class Application {
  constructor() {
    //additional initialization
    this.initialize();
  }

  initialize() {
    let self = this;
    this._mainView = new MainView();

    // this._pageController = new PageController({
    //   app: self
    // });

    this._homeController = new HomeController({
      app: self
    });

    this._cvController = new CvController({
      app: self
    });

    this._router = new Router({
      controllers: {
        // page: self._pageController,
        home: self._homeController,
        cv: self._cvController
      }
    });
    //start backbone history
    Backbone.history.start();
    console.log('app finished initializing!');
  }

  get router() {
    return this._router;
  }
}


//start up function
$(() => {
  //webpack works
  console.log('Loading...');

  let app = new Application();
  // console.log(app);
});



