/*
React nav component
*/

import Bars from 'react-bars';

export default class SkillBar extends React.Component {
  constructor(props) {
    super(props);

    this.style = {};

    this.state = {
      // api examples https://github.com/jniemann66/react-bars/blob/master/api.md
      skillData: [
        {label:'Python & Arcpy', value:85, barColor:'#1f8ce0'},
        {label:'Geographic Information Systems & Databases', value: 90 , barColor:'#1f8ce0'},
        {label:'Javascript & jQuery', value:75 , barColor:'#1f8ce0'},
        {label:'Node.js', value:80 , barColor:'#1f8ce0'},
        {label:'HTML5', value:96, barColor:'#1f8ce0'},
        {label:'CSS', value:94 , barColor:'#1f8ce0'},
        {label:'Bootstrap', value:81 , barColor:'#1f8ce0'},
        {label:'Web Development', value:75 , barColor:'#1f8ce0'},
        {label:'Bash', value:85 , barColor:'#1f8ce0'},
        {label:'CMD', value:82 , barColor:'#1f8ce0'}
      ]
    };
  }

  render() {
    var content = (
      <div className="skillset">
        <Bars data={this.state.skillData} makeUppercase={true}/>
      </div>
      );

      return content;
    }
  };



